@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;500;600&display=swap);

.app{
    background-image: url(/static/media/bg_03.3d06a355.svg);
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: center;
}

.btn-success{
    background: #3AA682 !important;
    border-color: #3AA682 !important;
}
.upload-img {
  width: 150px;
  height: 150px;
  border-radius: 5px;
  margin: 10px;

}
.preview-container {
  display: flex;
}
.x-button-container {
  position: relative;
}
.x-button {
  width: 20px;
  height: 20px;
  padding: 0;
  position: absolute;
  left: 139px;
  top: 11px;
  margin: 0;
  border: none;
  background-color: transparent;
}


.media img{
    height: 85%;
    width: 25%;
}

.media-body img{
    height: 15px;
    width: 15px;
}
*, body {
  font-family: 'Mulish', sans-serif;
} 
.main-container {
  padding: 0 10px;
  background-color: #E5E9EC;
}

.card-container-flex {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}
.card-img-top {
  height: 250px;
}
.card-body {
  padding: 0 !important;
}
.profile {
  margin: 20px;
}
span {
  font-weight: bold;
}
.icon {
  padding-top: 5px;
  width: 30px;
  height: 30px
}
.icon-text {
  display: flex;
  padding-top: 5px;
}
.alert-close {
  display: flex;
}
.leaflet-container {
    width: 100%;
    height: 100vh;
}
.img-home{
    height: 20px;
    position: relative;
    bottom: 3px;
}

.active{
    background: #00B7FF !important;
}

.btn-light{
    background:#FFBFC5 !important;
    color: white !important;
}

.btn-danger{
    background: #d73f3f !important;
    color: white !important;
}

.btn-info{
    background: lightskyblue !important;
    border: lightskyblue !important;
    color: white !important;
}

.img-fluid-sm{
    width:25px;
    height:25px;
    border-radius: 50%;
}
